import React, { useEffect, useState } from 'react';

import './UI.css';
import $ from 'jquery';

const UI = ( props ) => {

    const [ Form, setForm ] = useState(
        {
          studentName: '',
          FatherName: '',
          CNIC: '',
          MotherTongue: '',
          Occupation: '',
          MonthlyIncome: '',
          Surname: '',
          Religion: '',
          Gender: '',
          DateOfBirth: '',
          PlaceOfBirth: '',
          PreviousSchool: '',
          OfficeTelephone: '',
          ResidenceTelephone: '',
          Province: '',
          ResidentialAddress: ''
        }
      )

    useEffect(
        () => {

            setForm( props.Form )
            let inputs = document.querySelectorAll('.StudentEntryContainer .FormContainer .Section .FieldsContainer .Field input');

            $('.StudentEntryContainer .FormContainer .Section .FieldsContainer .Field input').on(
                'focus', ( e ) => {

                    onFocusInput( e.target.name );

                }
            );

            $('.StudentEntryContainer .FormContainer .Section .FieldsContainer .Field input').on(
                'blur', ( e ) => {

                    onBlurInput( e.target.name );

                }
            );

            for ( let x = 0; x < inputs.length; x++ )
            {

                inputs[x].addEventListener(
                    'change', ( e ) => {

                        props.onChangeHandler( e );

                    }
                )
                if ( inputs[x].value.length > 0 )
                {
                    console.log( console.log( inputs[x].name ) );
                }

            }

        }, [ props.Form ]
    )

    // WHEN FOCUS ON AN INPUT
    const onFocusInput = ( id ) => {

        $("#" + id).addClass('focusedLabel');

    }

    // WHEN BLUR ON AN INPUT
    const onBlurInput = ( id ) => {

        $("#" + id).removeClass('focusedLabel');

    }

  return (
    <div className="StudentEntryContainer">
        <div className="FormContainer">
            <div className="upper">
                <h2>Student Entry</h2>
                <p>Just enter the basic information about student</p>
            </div>

            <div className='Section'>
                <div className="title d-flex justify-content-center">
                    <p className="font-weight-bold mb-0">STUDENT INFORMATION</p>
                </div>
                <div className="FieldsContainer">

                    <div className="Field">
                        <label id="studentName"> Student Name </label>
                        <input type="text" name="studentName" 
                            onChange={ props.onChangeHandler }
                         />
                    </div>
                    <div className="Field">
                        <label id="Surname"> Surname </label>
                        <input type="text" name="Surname" />
                    </div>
                    <div className="Field">
                        <label id="Gender"> Gender </label>
                        <input type="text" name="Gender" />
                    </div>
                    <div className="Field">
                        <label id="MotherTongue"> Mother Tongue </label>
                        <input type="text" name="MotherTongue" 
                            onChange={ props.onChangeHandler }
                         />
                    </div>
                    <div className="Field">
                        <label id="DateOfBirth"> Date of Birth </label>
                        <input type="text" name="DateOfBirth" />
                    </div>
                    <div className="Field">
                        <label id="PlaceOfBirth"> Place of Birth </label>
                        <input type="text" name="PlaceOfBirth" />
                    </div>

                </div>
            </div>

        </div>
    </div>
  );

}

export default UI;